	text-decoration: underline
}

.static-page-text a {
	color: #000;
	font-weight: 500;
	text-decoration: underline
}

.btn-personal-data {
	display: none
}

.logged-in .btn-personal-data {
	display: inline-block
}

.mm-slideout {
	z-index: auto
}

.mm-menu {
	z-index: 9999 !important;
	background-color: #232323 !important;
	color: #000
}

.mm-menu .mm-navbar {
	background-color: #1a1a1a !important;
	color: #fff
}

.mm-menu .mm-btn:after,
.mm-menu .mm-btn:before {
	border-color: #fff !important
}

.mm-menu .mm-listview {
	font-size: 16px;
	line-height: 24px
}

.mm-menu .mm-listview>li {
	border-color: #000
}

.mm-menu .mm-listview>li>a,
.mm-menu .mm-listview>li>span {
	color: #fff;
	padding: 8px 10px 8px 20px
}

.mm-menu .mm-listview>li .mm-next::after {
	border-color: rgba(255, 255, 255, .3)
}

.mm-menu .mm-listview .fa {
	min-width: 26px;
	font-size: 16px;
	position: relative;
	top: -1px;
	color: #dc9125
}

.mm-clear::before,
.mm-close::before {
	right: 20px
}

#nav-mobile {
	box-shadow: 0 5px 10px rgba(0, 0, 0, .7)
}

#nav-mobile:not(.mm-menu) {
	display: none
}

#nav-mobile .mm-listview {
	font-size: 16px;
	line-height: 22px
}

.nav-mobile-logo {
	background-color: #1a1a1a;
	text-align: center
}

.nav-mobile-logo img {
	height: 50px
}

#nav-mobile-user {
	box-shadow: 0 -5px 10px rgba(0, 0, 0, .7)
}

#nav-mobile-user:not(.mm-menu) {
	display: none
}

#nav-mobile-user .login-form-errors {
	display: block;
	clear: both;
	margin-top: 20px;
	font-size: 14px
}

#nav-mobile-user .login-form-errors>b {
	position: static;
	width: 100%;
	margin-top: 20px;
	display: block
}

#nav-mobile-user ul>li>a {
	color: #fff
}

#nav-mobile-user .forgot-password {
	float: none;
	display: block;
	margin: 10px 0
}

#nav-mobile-user .header-top-buttons {
	display: block;
	float: none;
	margin-top: 20px
}

.nav-mobile-user-info {
	background-color: #333;
	font-size: 15px;
	padding-top: 10px !important
}

.nav-mobile-user-info span {
	color: #fff
}

.nav-mobile-user-info b {
	color: #eceeef;
	font-weight: 400;
	float: left
}

.nav-mobile-user-info .my-username {
	display: block;
	font-size: 18px
}

.nav-mobile-user-info .clear {
	display: block;
	clear: both
}

.nav-mobile-user-info .clear span {
	float: right
}

.nav-mobile-user-info .clear .js-username {
	font-weight: 700;
	font-size: 20px;
	color: #dc9125
}

.nav-mobile-user-info .clear .btn-refresh {
	color: #dc9125
}

.nav-mobile-user-info .user-balance-note {
	float: none;
	display: block;
	margin: -10px 0 0
}

.show-horizontal-table ::-webkit-scrollbar {
	width: 25px
}

.show-horizontal-table ::-webkit-scrollbar-track {
	background: #f1f1f1
}

.show-horizontal-table ::-webkit-scrollbar-thumb {
	background: #888
}

.show-horizontal-table ::-webkit-scrollbar-thumb:hover {
	background: #555
}

.promotion-item {
	display: block;
	border-radius: 3px;
	position: relative;
	border: 1px solid #b8b8b8;
	padding: 20px;
	margin: 8px;
	background: #ededed;
	transition: all .2s ease-in-out;
	text-decoration: none
}

.promotion-item .promotion-image-container {
	min-height: 350px
}

.promotion-item .promotion-image-container img {
	max-width: 100%
}

.promotion-item h2 {
	color: #000;
	font-size: 22px;
	line-height: 1.2em;
	font-weight: 500;
	margin: 4px
}

.promotion-item:hover {
	background: #dadada
}

.promotion-item b {
	padding: 2px 4px;
	background: #f2f2f2;
	border: 1px solid #b8b8b8;
	border-radius: 3px;
	color: #999;
	font-weight: 400;
	font-size: 14px
}

.terms-conditions {
	margin: 24px 0 8px 0
}

.article-title {
	margin-top: 16px
}

.article-conditions li {
	list-style: disc;
	margin-left: 20px;
	font-size: 14px;
	margin: 10px 20px
}

.promotion-item-img {
	max-width: 100%
}

#modal-flash {
	color: #fff;
	text-align: center;
	font-weight: 500
}

#modal-flash .modal-dialog {
	max-width: 600px;
	max-height: 500px
}

#modal-flash .modal-dialog .modal-content {
	background-color: rgba(0, 0, 0, .9);
	border-radius: 20px;
	padding: 30px 40px
}

#modal-flash .modal-dialog .modal-content .modal-header {
	margin: auto;
	font-size: 25px;
	width: 100%;
	padding-top: 5px;
	padding-bottom: 0
}

#modal-flash .modal-dialog .modal-content .modal-body {
	font-size: 15px;
	padding: 0;
	padding-top: 15px
}

#modal-flash .modal-dialog .modal-content .modal-body p {
	margin: 5px auto
}

#modal-flash .modal-dialog .modal-content .modal-footer {
	margin: auto;
	margin-top: 15px;
	font-size: 25px;
	border-top: 0
}

#modal-flash .modal-dialog .modal-content .modal-footer .btn {
	padding-left: 40px;
	padding-right: 40px;
	font-weight: 500
}

#modal-flash .modal-dialog .modal-content .modal-footer .btn.btn-primary {
	background-color: #fff;
	color: #222
}

#modal-flash .modal-dialog .modal-content .modal-footer .btn.btn-danger {
	background-color: #d9001d
}

#modal-flash .row {
	width: 100%;
	margin: auto
}

#modal-flash .row img {
	max-width: 75%;
	height: auto;
	margin: 10px auto
}

#modal-flash .row span {
	color: #d9001d
}

#modal-page .modal-sm {
	max-width: 700px
}

#modal-page .modal-sm .modal-header .row {
	width: 100%
}

#modal-page .modal-sm .modal-header .row .col-md-1 {
	position: absolute;
	right: 10px;
	top: 17px
}

#bonusPromptModal button.close {
	position: absolute;
	top: 4px;
	right: 4px;
	padding: 4px 8px;
	background-color: #f8eff0;
	transition: .3s;
	color: #1c1c1c
}

#bonusPromptModal button.close:hover {
	background-color: #ddd;
	color: #fff
}

