.switch {
  position: relative;
  display: inline-block;
  width: 51px;
  height: 27px;
  margin: 0;
}

.switch .switch__input {
  opacity: 0;
  width: 0;
  height: 0;
}

.switch__slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
}

.switch__slider:before {
  position: absolute;
  content: '';
  height: 20px;
  width: 20px;
  left: 5px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
}

.switch__input:checked + .switch__slider {
  background-color: #16663e;
}

.switch__input:focus + .switch__slider {
  box-shadow: 0 0 1px #0f673d;
}

.switch__input:checked + .switch__slider:before {
  -webkit-transform: translateX(20px);
  transform: translateX(20px);
}

.switch__slider.switch__input_round {
  border-radius: 24px;
}

.switch__slider.switch__input_round:before {
  border-radius: 50%;
}

