/* Ripple Bet effect */
.ripple-bet {
  background-position: center !important;
  transition: background 0.4s;
}

.ripple-bet:active {
  background-position: center !important;
  transition: background 0.2s;
}

.ripple-bet.anim {
  position: relative;
  transition: 0.2s;
}

.ripple-bet.anim.increased {
  -webkit-animation: border-green 6s;
          animation: border-green 6s;
}

.ripple-bet.anim.decreased {
  -webkit-animation: border-red 6s;
          animation: border-red 6s;
}

.ripple-bet.anim.increased:after,
.ripple-bet.anim.decreased:after {
  content: '';
  position: absolute;
  right: 0;
  display: inline-block;
  width: 0;
  height: 0;
  border-style: solid;
  -webkit-animation: triangle-anim 6s forwards;
          animation: triangle-anim 6s forwards;
}

.ripple-bet.anim.increased:after {
  top: 0;
  bottom: auto;
  border-width: 0 10px 10px 0;
  border-color: transparent #269137 transparent transparent;
}

.ripple-bet.anim.decreased:after {
  top: auto;
  bottom: 0;
  border-width: 0 0 10px 10px;
  border-color: transparent transparent #e30a17 transparent;
}

